import React, { useContext, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { loginUser, developement } from '../processes/userData';
import { UserContext } from '../App';


function Insert() {
      const history = useNavigate();
      const { state } = useContext(UserContext);
      const [inputValues, setInputValues] = React.useState({
            Sname: '', Age: '', ApplicationProcess: '', Benefits: '',
            Details: '',
            DocumentsRequired: '', FamilyIncome: '',
            ParentIncome: '', State: '', PersonalIncome: '',
            Occupation: ''
      });
      const HOC = event => {
            const { name, value } = event.target;
            setInputValues({ ...inputValues, [name]: value });
      };
      useEffect(() => {
            if (!state) {
                  history('/');
            }
      }, [state, history]);
      if (loginUser) {
            
            const Submit = async(e) => {
                  e.preventDefault();
                  let Gender = "";
                  if (document.getElementsByClassName('Gender')[0].checked) {
                        Gender = "Male";
                  }
                  if (document.getElementsByClassName('Gender')[1].checked) {
                        if (Gender.length !== 0) {
                              Gender += ",Female";
                        }
                        else {
                              Gender = "Female";
                        }
                  }
                  if (document.getElementsByClassName('Gender')[2].checked) {
                        if (Gender.length !== 0) {
                              Gender += ",Transgender";
                        }
                        else {
                              Gender = "Transgender";
                        }
                  }
                  if (Gender.length === 0) {
                        Gender = "0"
                  }

                  let Minority = '';
                  if (document.getElementsByClassName('Minority')[0].checked) {
                        Minority = "Yes";
                  }
                  else if (document.getElementsByClassName('Minority')[1].checked) {
                        Minority = "No";
                  }
                  else{
                        Minority = "0";
                  }

                  let Bpl = '';
                  if (document.getElementsByClassName('bpl')[0].checked) {
                        Bpl = "Yes";
                  }
                  else if (document.getElementsByClassName('bpl')[1].checked) {
                        Bpl = "No";
                  }
                  else{
                        Bpl = "0";
                  }

                  let Category = "";
                  if (document.getElementsByClassName('category')[0].checked) {
                        Category = "General";
                  }
                  if (document.getElementsByClassName('category')[1].checked) {
                        if (Category.length !== 0) {
                              Category += ",SC";
                        }
                        else {
                              Category = "SC";
                        }
                  }
                  if (document.getElementsByClassName('category')[2].checked) {
                        if (Category.length !== 0) {
                              Category += ",ST";
                        }
                        else {
                              Category = "ST";
                        }
                  }
                  if (document.getElementsByClassName('category')[3].checked) {
                        if (Category.length !== 0) {
                              Category += ",OBC";
                        }
                        else {
                              Category = "OBC";
                        }
                  }
                  if (Category.length === 0) {
                        Category = "0"
                  }

                  let DiffAbled = '';
                  if (document.getElementsByClassName('DA')[0].checked) {
                        DiffAbled = "Yes";
                  }
                  else if (document.getElementsByClassName('DA')[1].checked) {
                        DiffAbled = "No";
                  }
                  else{
                        DiffAbled = "0";
                  }

                  let Student = '';
                  if (document.getElementsByClassName('Stu')[0].checked) {
                        Student = "Yes";
                  }
                  else if (document.getElementsByClassName('Stu')[1].checked) {
                        Student = "No";
                  }
                  else{
                        Student = "0";
                  }

                  let MaritalStatus = '';
                  if (document.getElementsByClassName('MS')[0].checked) {
                        MaritalStatus = "Single";
                  }
                  if (document.getElementsByClassName('MS')[1].checked) {
                        if (MaritalStatus.length !== 0) {
                              MaritalStatus += ",Married";
                        }
                        else {
                              MaritalStatus = "Married";
                        }
                  }
                  if (document.getElementsByClassName('MS')[2].checked) {
                        if (MaritalStatus.length !== 0) {
                              MaritalStatus += ",Widowed";
                        }
                        else {
                              MaritalStatus = "Widowed";
                        }
                  }
                  if (document.getElementsByClassName('MS')[3].checked) {
                        if (MaritalStatus.length !== 0) {
                              MaritalStatus += ",Divorced";
                        }
                        else {
                              MaritalStatus = "Divorced";
                        }
                  }
                  if (MaritalStatus.length === 0) {
                        MaritalStatus = "0"
                  }

                  let AR = '';
                  if (document.getElementsByClassName('AR')[0].checked) {
                        AR = "Urban";
                  }
                  else if (document.getElementsByClassName('AR')[1].checked) {
                        AR = "Rural";
                  }
                  else{
                        AR = "0";
                  }                  

                  let Destitute = '';
                  if (document.getElementsByClassName('DE')[0].checked) {
                        Destitute = "Yes";
                  }
                  else if (document.getElementsByClassName('DE')[1].checked) {
                        Destitute = "No";
                  }
                  else{
                        Destitute = "0";
                  }

                  let EmployementStatus = '';
                  if (document.getElementsByClassName('ES')[0].checked) {
                        EmployementStatus = "Employed";
                  }
                  if (document.getElementsByClassName('ES')[1].checked) {
                        if (EmployementStatus.length !== 0) {
                              EmployementStatus += ",Un_employed";
                        }
                        else {
                              EmployementStatus = "Un_employed";
                        }
                  }
                  if (document.getElementsByClassName('ES')[2].checked) {
                        if (EmployementStatus.length !== 0) {
                              EmployementStatus += ",Gov_employed";
                        }
                        else {
                              EmployementStatus = "Gov_employed";
                        }
                  }
                  if (document.getElementsByClassName('ES')[3].checked) {
                        if (EmployementStatus.length !== 0) {
                              EmployementStatus += ",Self_employed";
                        }
                        else {
                              EmployementStatus = "Self_employed";
                        }
                  }
                  if (EmployementStatus.length === 0) {
                        EmployementStatus = "0"
                  }
                  
			const result = await fetch((developement) ? "http://localhost:5000/Insert" : "/Insert", {

				method: "POST",
				crossDomain: true,
				headers: {
					"Content-Type": "application/json",
					Accept: "application/json",
					"Access-Control-Allow-Origin": "*",
				},
				body: JSON.stringify({
					Sname: inputValues.Sname?inputValues.Sname:'0', Age: inputValues.Age?inputValues.Age:'0', 
                              ApplicationProcess: inputValues.ApplicationProcess?inputValues.ApplicationProcess:'0', 
                              Benefits: inputValues.Benefits?inputValues.Benefits:'0', Details: inputValues.Details?inputValues.Details:'0',
                              DocumentsRequired: inputValues.DocumentsRequired?inputValues.DocumentsRequired:'0',
                              FamilyIncome: inputValues.FamilyIncome?inputValues.FamilyIncome:'0',
                              ParentIncome: inputValues.ParentIncome?inputValues.ParentIncome:'0',
                              State: inputValues.State?inputValues.State:'0',
                              PersonalIncome: inputValues.PersonalIncome?inputValues.PersonalIncome:'0',
                              Occupation: inputValues.Occupation?inputValues.Occupation:'0',
                              Gender, Minority, Bpl, Category, DifferentlyAbled: DiffAbled,
                              Student, MaritalStatus, AreaOfResidence: AR, 
                              Destitute, EmployementStatus
				})

			})
			if (result.status === 500) {
				alert('Internal Server Error')
			}
			else if (result.status === 201) {
				alert("Successfully inserted")
			}
			else{
				alert("unknown error");
			}
            }
            return (
                  <>
                        <div className="main">
                              <div className="main-card">
                                    <h2>Insert a Scheme</h2>
                                    <input type="text" name="Sname" onChange={HOC} placeholder="Scheme Name" />
                                    <br />
                                    <input type="checkbox" className='Gender' value="Male" />Male
                                    <input type="checkbox" className='Gender' value="Female" />Female
                                    <input type="checkbox" className='Gender' value="Transgender" />Transgender
                                    <br />
                                    <input type="text" name="Age" onChange={HOC} placeholder="<28 / >28" />
                                    <br />
                                    <textarea name="ApplicationProcess" onChange={HOC} placeholder="ApplicationProcess" />
                                    <br />
                                    <br />
                                    Belongs to minority?
                                    <br />
                                    <input type="checkbox" className='Minority' />Yes
                                    <input type="checkbox" className='Minority' /> No
                                    <br />
                                    <input type="text" name="Benefits" onChange={HOC} placeholder="Benefits" />
                                    <br />
                                    Belongs to BplCategory ?
                                    <br />
                                    <input type="checkbox" className='bpl' />Yes
                                    <input type="checkbox" className='bpl' /> No
                                    <br />
                                    <br />
                                    Category ?
                                    <br />
                                    <input type="checkbox" className='category' />General
                                    <input type="checkbox" className='category' />SC
                                    <input type="checkbox" className='category' />ST
                                    <input type="checkbox" className='category' />OBC
                                    <br />
                                    <input type="text" name="Details" onChange={HOC} placeholder="Details of Scheme" />
                                    <br />
                                    Differently Abled ?
                                    <br />
                                    <input type="checkbox" className='DA' />Yes
                                    <input type="checkbox" className='DA' /> No
                                    <br />
                                    <input type="text" name="DocumentsRequired" onChange={HOC} placeholder="Required documents list" />
                                    <br />
                                    <input type="text" name="FamilyIncome" onChange={HOC} placeholder="FamilyIncome (<1000 OR >1000)" />
                                    <br />
                                    <input type="text" name="ParentIncome" onChange={HOC} placeholder="Parent Income (<1000 OR >1000)" />
                                    <br />
                                    <input type="text" name="PersonalIncome" onChange={HOC} placeholder="Personal Income (<1000 OR >1000)" />
                                    <br />
                                    <input type="text" name="State" onChange={HOC} placeholder="State" />
                                    <br />
                                    For Students ?
                                    <br />
                                    <input type="checkbox" className='Stu' />Yes
                                    <input type="checkbox" className='Stu' />No
                                    <br />
                                    <br />
                                    Marital status ?
                                    <br />
                                    <input type="checkbox" className='MS' />Single
                                    <input type="checkbox" className='MS' />Married
                                    <br />
                                    <input type="checkbox" className='MS' />Widowed
                                    <input type="checkbox" className='MS' />Divorced
                                    <br />
                                    <br />
                                    Area of Residence ?
                                    <br />
                                    <input type="checkbox" className='AR' />Urban
                                    <input type="checkbox" className='AR' />Rural
                                    <br />
                                    <br />
                                    Destitute ?
                                    <br />
                                    <input type="checkbox" className='DE' />Yes
                                    <input type="checkbox" className='DE' />No
                                    <br />
                                    <br />
                                    Employement Status ?
                                    <br />
                                    <input type="checkbox" className='ES' />Employed
                                    <input type="checkbox" className='ES' />UnEmployed
                                    <br />
                                    <input type="checkbox" className='ES' />Govt. Employee
                                    <input type="checkbox" className='ES' />Self Employed
                                    <br />
                                    <input type="button" className='goHome' id='Sub' value="Submit" onClick={Submit} />
                              </div>

                        </div>
                  </>
            )
      }
      else {
            return (
                  <>
                        Error
                  </>
            )
      }
}

export default Insert;