import React, { createContext, useReducer, useLayoutEffect } from 'react';
import { Route, Routes } from 'react-router-dom';

import './index.css';
import './App.css'
import 'bootstrap/dist/css/bootstrap.css';

import Navbar from './components/Navbar';
import Home from './components/Home';
import Insert from './components/Insert';


export const UserContext = createContext();

function reducer(state, action) {
	if (action.type === 'USER') {
		return action.payload;
	}
	return state;
}

function Routing() {
	return (
		<>
			<Routes>
				<Route exact path="/Insert" element={ <Insert /> } />
				<Route exact path='/' element={<Home />} />
				{/* <Route path='*' element={<Error />} /> */}
			</Routes>
		</>
	)
}



function App(Props) {

	const [state, dispatch] = useReducer(reducer, true);
	
	useLayoutEffect(() => {
		// console.log("APP: " + Props.what)
		dispatch({ type: "USER", payload: Props.what })
	}, [Props.what])

	return (
		<>
			<UserContext.Provider value={{ state, dispatch }}>
				<Navbar />
				<Routing />
			</UserContext.Provider>
		</>
	)




}

export default App